import React, { useState } from "react";

import {
  UncontrolledAccordion,
  AccordionBody,
  AccordionHeader,
  AccordionItem,
} from "reactstrap";

import { octokit } from "./octokit";

import { listUserType } from "./Form";
import ListRepoComp from "./ListRepoComp";

interface ListUserCompType {
  listUser: listUserType[];
}

export interface listRepoType {
  id: number;
  description: string;
  name: string;
}

export default function ListUserComp(props: ListUserCompType) {
  const [listRepo, setListRepo] = useState<listRepoType[]>([]);
  const [loading, setLoading] = useState<boolean>(false);
  const [urlRepo, setUrlRepo] = useState<string>("");

  function getUserRepo(repos_url: string) {
    setLoading(true);
    setListRepo([]);

    octokit
      .request(`GET ${repos_url}`)
      .then((res) => {
        console.log(res);
        setLoading(false);
        setListRepo(res.data);
      })
      .catch((err) => console.log(err));
  }

  function AccordionClick(uRepos: string) {
    if (uRepos !== urlRepo) {
      setUrlRepo(uRepos);
      getUserRepo(uRepos);
    }
  }

  return (
    <>
      <div className="div_list_user">
        <UncontrolledAccordion className="accordion_user" defaultOpen={"200"}>
          {props.listUser &&
            props.listUser.map((data, index) => (
              <React.Fragment key={index}>
                <AccordionItem>
                  <AccordionHeader
                    onClick={() => AccordionClick(data.repos_url)}
                    targetId={index.toString()}
                    className="accordion_header"
                  >
                    {data.login}
                  </AccordionHeader>
                  <AccordionBody accordionId={index.toString()}>
                    {loading ? (
                      <div>Loading...</div>
                    ) : (
                      <ListRepoComp listRepo={listRepo} />
                    )}
                  </AccordionBody>
                </AccordionItem>
              </React.Fragment>
            ))}
        </UncontrolledAccordion>
      </div>
    </>
  );
}
