import React from "react";

import { listRepoType } from "./ListUserComp";

interface ListRepoCompType {
  listRepo: listRepoType[];
}

export default function ListRepoComp(props: ListRepoCompType) {
  return (
    <>
      {props.listRepo &&
        props.listRepo.map((data, index) => (
          <div className="div_repo" key={index}>
            <div className="div_repo_name">{data.name}</div>
            <div className="div_repo_desc">{data.description}</div>
          </div>
        ))}
    </>
  );
}
