import React, { useState } from "react";

import ListUserComp from "./ListUserComp";

import { octokit } from "./octokit";

export interface listUserType {
  login: string;
  id: number;
  repos_url: string;
}

export default function Form() {
  const [username, setUsername] = useState<string>("");
  const [loading, setLoading] = useState<boolean>(false);
  const [notFound, setNotFound] = useState<boolean>(false);
  const [listUser, setListUser] = useState<listUserType[]>([]);

  function getUser(e: React.SyntheticEvent) {
    setLoading(true);
    setListUser([]);
    e.preventDefault();

    octokit
      .request("GET /search/users?q=" + username)
      .then((res) => {
        console.log(res);
        if (res.data.items.length > 0) {
          setListUser(res.data.items);
          setNotFound(false);
        } else {
          setNotFound(true);
        }
        setLoading(false);
      })
      .catch((err) => console.log(err));
  }

  return (
    <>
      <div className="form_container">
        <div className="form_card">
          <form onSubmit={getUser}>
            <input
              required
              type="text"
              maxLength={200}
              pattern="[a-zA-Z0-9]+"
              placeholder="Enter username"
              className="input_username"
              onChange={(e) => setUsername(e.target.value)}
            />
            <button type="submit" className="btn_search">
              Search
            </button>
          </form>

          <div className="div_info">
            {loading
              ? "Loading..."
              : notFound
              ? "User not found"
              : listUser.length > 0 &&
                `
            Showing users for "${username}"
          `}
          </div>

          <ListUserComp listUser={listUser} />
        </div>
      </div>
    </>
  );
}
